<template>
  <li v-if="!item.disabled">
    <router-link :to="item.route" :class="['nav-item', { 'nav-item--active': item.active }]">
      <TreeMarker v-if="isSubitem" class="nav-item__tree-marker" :last="type === 'lastSubitem'" />
      <span :class="['ty-forms-text', 'nav-item__title', { 'nav-item__title--subitem': isSubitem }]">{{ item.title }}</span>
    </router-link>
  </li>
</template>

<script setup lang="ts">
import TreeMarker from "@/shared/ui_kit/TreeMarker.vue";
import type { SidebarItemType } from "@/shared/ui_kit/sidebar-nav/types/SidebarNavItemType";
import { object, string } from "vue-types";
import { computed } from "vue";

const props = defineProps({
  item: object<SidebarItemType>().isRequired,
  type: string<"default" | "subitem" | "lastSubitem">().def("default"),
});

const isSubitem = computed(() => props.type === "subitem" || props.type === "lastSubitem");
</script>

